  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 50%;
  margin: 20px auto;
  top: 20px;
}

.nav-logo {
  float: left;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  font-size: 20px;
  font-weight: bolder;
  height: 100%;
}

.nav-ul {
  width: 40%;
  height: 100%;
  float: right;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: row;
          flex-direction: row;
  text-transform: uppercase;
}

.nav-ul li {
  position: relative;
}

.nav-ul li:not(:last-child)::after {
  content: "";
  background-color: #1da1f2;
  width: 2px;
  height: 100%;
  position: absolute;
  top: 0px;
  right: -100%;
}

/* #24252a;  */
.nav-ul .link {
  color: #ffffff;
  text-decoration: none;
  position: relative;
  font-weight: bolder;
}

.nav-ul a::after {
  content: "";
  width: 0%;
  height: 3px;
  position: absolute;
  top: 30px;
  left: 0px;
  background-color: #f23abe;
  transition: ease-in-out 0.5s;
}
.nav-ul a:hover::after {
  width: 100%;
}

#nav-toggle {
  position: absolute;
  top: -100px;
}
nav .icon-burger {
  display: none;
  position: absolute;
  right: 15%;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
nav .icon-burger .line {
  width: 25px;
  height: 3px;
  background-color: #24252a;
  margin: 5px;
  border-radius: 2px;
  transition: all 0.3s ease-in-out;
}

@media screen and (max-width: 1000px) {
  .nav-ul li:not(:last-child)::after {
    right: -20%;
  }
}
@media screen and (max-width: 1000px) {
  .nav-logo {
    float: none;
    -webkit-justify-content: center;
            justify-content: center;
    width: auto;
  }

  nav .icon-burger {
    display: block;
  }
  .nav-ul {
    float: none;
    position: fixed;
    bottom: 100%;
    top: 70px;
    left: 0px;
    right: 0px;
    z-index: 100;
    width: auto;
    height: auto;
    -webkit-flex-direction: column;
            flex-direction: column;
    transition: ease-in-out 0.7s;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    background-color: rgba(0, 0, 0, 0.4);
    overflow: hidden;
    box-sizing: border-box;
  }
  .nav-ul li a {
    color: #dedede;
  }

  .nav-ul li:not(:last-child)::after {
    display: none;
  }
  .icon-burger {
    display: -webkit-flex;
    display: flex;
  }

  nav :checked ~ .nav-ul {
    bottom: 0%;
  }
  nav :checked ~ .icon-burger .line:nth-child(1) {
    -webkit-transform: translateY(8px) rotate(225deg);
            transform: translateY(8px) rotate(225deg);
  }
  nav :checked ~ .icon-burger .line:nth-child(3) {
    -webkit-transform: translateY(-8px) rotate(-225deg);
            transform: translateY(-8px) rotate(-225deg);
  }
  nav :checked ~ .icon-burger .line:nth-child(2) {
    opacity: 0;
  }
}

